import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { Chessboard } from 'react-chessboard';
import { Chess } from 'chess.js';
import { socket } from '../socket';
import axios from 'axios';

const Game = () => {
  const { gameId } = useParams();
  const [game, setGame] = useState(new Chess());
  const [color, setColor] = useState('w');
  const [player1Name, setPlayer1Name] = useState('');
  const [player2Name, setPlayer2Name] = useState('');

  useEffect(() => {
    const token = localStorage.getItem('token');

    socket.auth = { token };
    socket.connect();

    socket.emit('joinGame', gameId);

    socket.on('move', (move) => {
      const newGame = new Chess(game.fen());
      newGame.move(move);
      setGame(newGame);
    });

    return () => {
      socket.disconnect();
    };
  }, [gameId]);

  useEffect(() => {
    const fetchGame = async () => {
      const response = await axios.get(`https://tfg-back.onrender.com/api/games/${gameId}`, {
        headers: { 'x-auth-token': localStorage.getItem('token') }
      });
      const savedGame = new Chess(response.data.boardState);
      setGame(savedGame);

      // Obtener nombres de usuario de player1 y player2
      const player1 = await axios.get(`https://tfg-back.onrender.com/api/auth/user/${response.data.player1}`, {
        headers: { 'x-auth-token': localStorage.getItem('token') }
      });
      setPlayer1Name(player1.data.name);

      if (response.data.player2) {
        const player2 = await axios.get(`https://tfg-back.onrender.com/api/auth/user/${response.data.player2}`, {
          headers: { 'x-auth-token': localStorage.getItem('token') }
        });
        setPlayer2Name(player2.data.name);
      }

      // Determina el color del jugador actual
      const userId = JSON.parse(atob(localStorage.getItem('token').split('.')[1])).user.id;
      if (response.data.player1 === userId) {
        setColor('w');
      } else {
        setColor('b');
      }
    };

    fetchGame();
  }, [gameId]);

  const handleMove = (sourceSquare, targetSquare) => {
    const newGame = new Chess(game.fen());
    const move = newGame.move({
      from: sourceSquare,
      to: targetSquare,
      promotion: 'q',
    });
    if (move === null) return;

    setGame(newGame);

    socket.emit('move', {
      gameId,
      move,
      fen: newGame.fen(),
      turn: newGame.turn(),
      result: newGame.game_over() ? (newGame.in_checkmate() ? 'checkmate' : 'draw') : 'ongoing',
    });
  };

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-gray-100 p-6">
      <div className="w-full max-w-4xl bg-white rounded-lg shadow-md p-6">
        <div className="text-center mb-4">
          <h2 className="text-2xl font-bold mb-2">{player1Name || 'Jugador 1'} (Blancas)</h2>
          <h2 className="text-2xl font-bold mb-2">{player2Name || 'Jugador 2'} (Negras)</h2>
        </div>
        <Chessboard
          position={game.fen()}
          onPieceDrop={(sourceSquare, targetSquare) => handleMove(sourceSquare, targetSquare)}
          orientation={color === 'w' ? 'white' : 'black'}
          className="mb-4"
        />
        {/* <Chat gameId={gameId} /> */}
      </div>
    </div>
  );
};

export default Game;
